<template lang="html">
  <div class="Add-company">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="3" class="font-16">
          <h3>{{$t("common.Add_user")}}</h3>
        </Col>
        <Col span="5">

        </Col>
        <Col span="5">

        </Col>
        <Col span="8">
          <Button type="info" class="mgl-10" @click="succ">{{$t("common.Add")}}</Button>
          <Button type="warning" class="mgl-20" @click="cancel">{{$t("common.Cancel")}}</Button>
        </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>{{$t("common.Username")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>{{$t("common.E_mail")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>{{$t("common.Password")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>{{$t("common.phonenum")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>{{$t("common.Confirm_password")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>{{$t("common.Role_type")}} <b>*</b></span>
        <Select style="width:200px;text-align: center;">
            <Option v-for="item in category" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>{{$t("common.Role_power")}} <b>*</b></span>
        <Select style="width:200px;text-align: center;">
            <Option v-for="item in power" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
      </Col>
      <Col span="10" class="text-right">
        <span>{{$t("common.Role_belong")}} <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
  </div>
</template>

<script>
export default {
  data (){
    return {
      ContractName:'',
      ContractUse:'',
      editorContent: '',
      category: [
          {
              value: 'Yes',
              label: this.$t("common.Ordinary_role")
          },
          {
              value: 'No',
              label: this.$t("common.Management_role")
          }
      ],
      power: [
          {
              value: 'ordinary',
              label: this.$t("common.General_power")
          },
          {
              value: 'basic',
              label: this.$t("common.Basic_power")
          },
          {
              value: 'senior',
              label: this.$t("common.Senior_power")
          },
          {
              value: 'super',
              label: this.$t("common.Super_power")
          }
      ],
    }
  },
  methods:{
    succ () {
      this.$Message.success(this.$t("common.Add_success"));
      setTimeout(() =>{
        this.$router.push('/System_user');
      },2000)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.ContractName = routerParams.name
          this.ContractUse = routerParams.use
          this.editorContent = routerParams.content
        }
    },
    cancel () {
      this.$router.push('/System_user');
    }
  },
  mounted() {
    this.getParams ()
  }
}
</script>

<style lang="css">
.Add-company{
  padding-top: 70px;
  width: 1200px;
  margin: 0 auto;
}
.Add-company .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
}
.Add-company .contract-row{
  line-height: 30px;
  margin-top: 20px;
}
.Add-company .ivu-select-dropdown{
  z-index: 99999
}
.Add-company .contract-row b{
  color: #ed3f14;
}
</style>



